<template>
    <v-main>
            <h2 class="text-center">Vous pouvez etre interesse par ces Posts .......</h2>
        <v-row>
          <v-col v-for="item in varView" :key="item.id" cols="12" md="4">
            <v-card outlined class="mx-auto">
              <v-img
                class="white--text align-end"
                height="200px"
                :src="`/${item.image}`"
              >
                <v-card-title>{{ item.title }}</v-card-title>
              </v-img>
              <v-card-subtitle class="pb-0">{{ item.category }}</v-card-subtitle>
              <v-card-text class="text--primary">
                <code>
                  {{ item.content.substring(0, 100).replace("<","").replace("span","").replace("style=\"font-size: 14pt;\"","").replace("p>< >","")}}
                </code>
              </v-card-text>
              <v-card-actions>
                <v-btn color="primary" text :to="{name:'post', params: {id:item._id}}">lire Plus</v-btn>
              </v-card-actions>
            </v-card>
          </v-col>
        </v-row>
        <div class="block">
            <h2 class="text-center">Faite une remarque a propos du Post a travers ce formulaire</h2>
        <v-row>
          <v-col cols="12" >
          
                <v-form ref="form" v-model="valid" lazy-validation>
                        <v-text-field v-model="name" :counter="10" :rules="nameRules" label="Name" required></v-text-field>
                        <v-text-field v-model="email" :rules="emailRules" label="E-mail" required></v-text-field>
                        <v-textarea v-model="message" :rules="messageRules" label="Message" required></v-textarea>
                        <v-btn :disabled="!valid" color="success" class="mr-4" @click="validate">Envoyer</v-btn>
                        <v-btn color="error" class="mr-4" @click="reset">vider</v-btn>
                </v-form>
                
          
          </v-col>
        </v-row>
        </div>
      
    </v-main>

</template>
<script>
import API from "../api";
export default {
    name: "postandComment",
    props:{
        categoryParent:String
    },
    data: () => ({
        valid: true,
        name:"",
        email:"",
        message:"",
        nameRules: [
            v => !!v || "Name is required",
            v => (v && v.length <= 10) || "Name must be less than 10 characters"
            ],
        emailRules: [
            v => !!v || "E-mail is required",
            v => /.+@.+\..+/.test(v) || "E-mail must be valid"
            ],
        messageRules: [
            v => !!v || "Message is required",
            v => (v && v.length >= 10) || "Message must be more than 10 characters"
            ],
        items:[],
        varTemp:[],
        varView:[]
    }),
    async created() {
    this.items = await API.getAllPost();
    console.log(this.items);
    for (let i = 0; i <  this.items.length; i++) {
            if (this.items[i].category ==this.categoryParent) {
            this.varTemp.push(this.items[i])
            console.log('@@@@this.varTemp',this.varTemp);
            }
    }

    for (let i = 0; i < 3; i++) {
      //debugger
      if (this.varTemp[i] != null && this.varTemp[i] != undefined) {
        this.varView.push(this.varTemp[i]);
        console.log('@@@@ this.varView', this.varView);

      }
    }
  },
  methods: {
    validate() {
      if (this.$refs.form.validate()) {
        this.snackbar = true;
      }
    },
    reset() {
      this.$refs.form.reset();
    }
  }
    }
</script>