<script>
import API from "../components/api"
export default {
  name: "LatestPost",
  data: () => ({
    displayCard:false,
    displaySpinner:true,
    show: false,
    ApexHome: [],
    ApexTemp: [],
    LwcHome: [],
    lwcTemp: [],
    VFHome: [],
    VFTemp: [],
    AdminHome: [],
    AdminTemp: [],
    items: []
  }),
  async created() {
    this.items = await API.getAllPost();
    this.displayCard = true;
    this.displaySpinner = false;
    console.log(this.items);
    for (let i = 0; i < this.items.length; i++) {
      if (this.items[i].category == "Apex") {
        this.ApexTemp.push(this.items[i])
        console.log('this.ApexTemp', this.ApexTemp);
      } else if (this.items[i].category == "LWC") {
        this.lwcTemp.push(this.items[i])
        console.log('this.lwcTemp', this.lwcTemp);
      } else if (this.items[i].category == "visualforce") {
        this.VFTemp.push(this.items[i])
        console.log('this.lwcTemp', this.VFTemp);
      } else if (this.items[i].category == "Administration") {
        this.AdminTemp.push(this.items[i])
        console.log('this.lwcTemp', this.AdminTemp);
      }
    }
      for (let i = 0; i < 3; i++) {
        //debugger
        if (this.ApexTemp[i] != null && this.ApexTemp[i] != undefined) {
          this.ApexHome.push(this.ApexTemp[i]);
        }
        if (this.lwcTemp[i] != null && this.lwcTemp[i] != undefined) {
          this.LwcHome.push(this.lwcTemp[i]);
        }
        if (this.VFTemp[i] != null && this.VFTemp[i] != undefined) {
          this.VFHome.push(this.VFTemp[i]);
        }
        if (this.AdminTemp[i] != null && this.AdminTemp[i] != undefined) {
          this.AdminHome.push(this.AdminTemp[i]);
        }
      }
  }
}
</script>
