import Vue from 'vue'
import VueRouter from 'vue-router'
import HomeView from '../views/HomeView.vue'
// import postView from "../components/postView.vue";
import ApexView from "../views/ApexView.vue";
import LwcView from "../views/LwcView.vue";
import VisualForceView from "../views/VisualForceView.vue";
import AdminView from "../views/AdminView.vue";
import AboutView from "../views/AboutView.vue";
import PostCardView from "../views/postCardView.vue";

Vue.use(VueRouter)

const routes = [
  {
    path: '/',
    name: 'home',
    component: HomeView
  },
  {
    path: '/ApexView',
    name: 'Apex',
    component: ApexView
  },
  {
    path: '/LwcView',
    name: 'LWC',
    component: LwcView
  },
  {
    path: '/VisualForceView',
    name: 'VisualForcePage',
    component: VisualForceView
  },
  {
    path: '/AdminView',
    name: 'Administration',
    component: AdminView
  },
  {
    path: '/AboutView',
    name: 'About',
    component: AboutView
  },
  {
    path: '/post/:id',
    name: 'post',
    component: PostCardView
  }
]

const router = new VueRouter({
  mode: 'history',
  base: process.env.BASE_URL,
  routes
})

export default router
