<template>
    <v-content>
        <div class="staticHero">
            <v-img src="../assets/images/img13.jpg">
                <v-row align="end" class="lightbox white--text pa-2 fill-height">
                    <v-col>
                        <v-container>
                            <div class="headline">Apex </div>
                        </v-container>
                    </v-col>
                </v-row>
            </v-img>
        </div>
        <div class="block">
            <v-container>
                <p class="contenu">Apex est un langage de programmation base cote serveur permettant d'implementer des
                    logiques en servant des operations tel que le DML ,SOQL,SOSL, et bien d'autres.</p>
            </v-container>
        </div>
        <div class="latestPostBlock spinner">
            <v-progress-circular 
                :size="100"
                :width="15"
                color="blue"
                indeterminate 
                v-if="displaySpinner">
            </v-progress-circular>
        </div>
        <div class="block" v-if="displayCard">
            <v-container>
                <v-row>
                    <v-col v-for="item in ApexView" :key="item.id" cols="12" md="4">
                        <v-card outlined class="mx-auto">
                            <v-img class="white--text align-end" height="200px" :src="item.image">
                                <!-- <v-card-title style="color:black">{{ item.title }}</v-card-title> -->
                            </v-img>
                            <v-card-subtitle class="pb-0">{{ item.category }}</v-card-subtitle>
                            <v-card-text class="text--primary">
                                <code>
                                    {{ item.content.substring(0, 100).replace("<","").replace("span","").replace("style=\"font-size: 14pt;\"","").replace("p>< >","")}}
                                </code>
                            </v-card-text>
                            <v-card-actions>
                                <v-btn color="primary" text :to="{ name: 'post', params: { id: item._id } }">lire Plus</v-btn>
                            </v-card-actions>
                        </v-card>
                    </v-col>
                </v-row>
            </v-container>
        </div>
    </v-content>
</template>
  
<script>

import API from "../components/api"
export default {
    name: "About",
    data() {
        return {
            displayCard:false,
            displaySpinner:true,
            items: [],
            ApexView: []
        };
    },
    async created() {
        this.items = await API.getAllPost();
        this.displayCard = true;
        this.displaySpinner = false;
        console.log(this.items);
        for (let i = 0; i < this.items.length; i++) {
            if (this.items[i].category == "Apex") {
                this.ApexView.push(this.items[i])
                console.log('this.ApexTemp', this.ApexTemp);
            }
        }
    }
};
</script>

<style>
.contenu {
    font-size: large;
    font-weight: lighter;
    font-family: Verdana, Geneva, Tahoma, sans-serif;
}

.spinner{
  display: block;
  align-items: center;
  margin-left: 55em;
  margin-top: 5em;
  padding: 60px 0;
}

@media (min-width: 190px) and (max-width: 300px) {
