<template>
    <v-container>
      <v-row no-gutters>
         <v-col sm="10" class="pa-4 mx-auto">
             <v-card class="pa-2">
                 <v-img height="600" :src="`/${post.image}`"></v-img>
                 <v-card-actions class="pb-0">
                       <v-row class="mt-1 mx-1">
                           <v-col sm="2">
                               <v-btn small outlined color="primary">{{post.category}}</v-btn>
                           </v-col>
                           <v-col sm="10" class="d-flex justify-end">
                            <v-avatar color="indigo">
                                <v-icon dark>
                                    mdi-account-circle
                                </v-icon>
                            </v-avatar>
                            <div class="pl-10">
                                posté le {{ post.createdDate}}
                            </div>     
                           </v-col>
                       </v-row>
                 </v-card-actions>
                 <v-card-subtitle class="headkine">
                      <h4 class="title-h4"> {{post.title}}</h4>
                 </v-card-subtitle>
                 <v-card-text class="grey--text"> 
                    <!-- <p id="textarea-content">{{post?.content}}</p> -->
                    <p id="textarea-content"></p>
                    <p>{{post?.created}}</p>
                 </v-card-text>
             </v-card>
         </v-col>
      </v-row>
      <v-row>
        <v-col sm="10"  class="pa-4 mx-auto">
           <relatedAndComment :categoryParent="post.category"/>
        </v-col>
      </v-row>
    </v-container>
 </template>
 
 <script>
 import API from '../api';
 import relatedAndComment from "../components/relatedAndComment.vue";
  export default {
     data(){
         return{
             post:{}
         }
     },
     async created(){
      
         this.post = await API.getAllPostById(this.$route.params.id);
         console.log('@@@@@response ',this.post);
         document.getElementById('textarea-content').innerHTML = this.post.content;
     },
     components:{
        relatedAndComment,
     }
  }
 </script>
 <style>
.title-h4{
    color: black;
}
</style>